import React from "react";
import Sun from "../assets/img/sun.png";
import Rain from "../assets/img/rain.png";


function card_day(){
    return(
        <div className="card_day" align="center">
            <strong>{this.props.day}</strong>
            <p>{this.props.date}</p>
            <img src={this.props.weather} />
            <strong>{this.props.temp}</strong>
            <a className="button_detail">+</a>
        </div>
    )
}

function cards(){
    return(
        <div className="cards" align="center">
            <div className="card_day" align="center">
                <strong>Lunes</strong>
                <p>24 de sep</p>
                <img src={Sun} />
                <br/>
                <strong>20°</strong>
                <hr/>
                <div class="weather_hourly">
                    <div>
                        <p>08:00</p>
                        <img src={Sun} />
                        <p>22°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Rain} />
                        <p>27°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Sun} />
                        <p>22°</p>
                    </div>
                </div>
            </div>

            <div className="card_day" align="center">
                <strong>Martes</strong>
                <p>25 de sep</p>
                <img src={Rain} />
                <br/>
                <strong>20°</strong>
                <div class="weather_hourly">
                    <div>
                        <p>08:00</p>
                        <img src={Rain} />
                        <p>22°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Sun} />
                        <p>27°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Rain} />
                        <p>22°</p>
                    </div>
                </div>
            </div>

            <div className="card_day" align="center">
                <strong>Miercoles</strong>
                <p>26 de sep</p>
                <img src={Sun}/>
                <br/>
                <strong>20°</strong>
                <div class="weather_hourly">
                    <div>
                        <p>08:00</p>
                        <img src={Sun} />
                        <p>22°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Rain} />
                        <p>27°</p>
                    </div>
                    <div>
                        <p>14:00</p>
                        <img src={Sun} />
                        <p>22°</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default cards



