import './App.css';
import React from 'react';
import Day from './../src/assets/img/day5.png';
import Cards from './Components/cards';

var hour = 13;
var minutes = 12;
var time = hour+":"+minutes;
class Capsule_strong extends React.Component{
  render(){
    return <div id={this.props.id} className="header_component" align="center"><strong>{this.props.content}</strong></div>;
  }
}
